import React, { memo, useState, useEffect } from "react";
import {
  Modal,
  Form,
  Input,
  Select,
  Button,
  Space,
  Row,
  Col,
  message,
  InputNumber,
} from "antd";
import { Tree } from "antd";
import { room_pool_rules_config_update } from "../../../../../../api/room";

// import { room_pool_rules_config_update } from "../../../api/room";
const { Option } = Select;
const { TextArea } = Input;
const Index = (props) => {
  const { onCancel, visible, onOk, info,rid } = props;
  const [form] = Form.useForm();
  //
  const submit = async () => {
    // console.log(form.getFieldsValue());
    try {
      form.validateFields().then(async () => {
        await room_pool_rules_config_update({
          ...form.getFieldsValue(),
          rule_id: info.rule_id,
        });
        message.success("");
        onOk();
        form.resetFields();
      });
    } catch (e) {
      console.error(e);
    }
  };
  const FormConfig = {
    labelCol: { span: 7 },
    wrapperCol: { span: 17 },
  };

  const on_cancel = () => {
    onCancel();
    form.resetFields();
  };
  useEffect(() => {
    if (visible) {
      form.setFieldsValue({
        ...info,
        rid
      });
    }
  }, [visible]);
  return (
    <Modal
      title=""
      visible={visible}
      onCancel={on_cancel}
      onOk={submit}
    >
      <Form
        {...FormConfig}
        form={form}
        name="form_in_modal"
        hideRequiredMark={true}
      >
        <Form.Item
          label="id"
          name="rid"
          rules={[{ required: true, message: "id" }]}
        >
          <InputNumber disabled placeholder="id" />
        </Form.Item>
        <Form.Item
          label=""
          name="flag"
          rules={[{ required: true, message: "" }]}
        >
          <Select placeholder="">
            <Option value={1}></Option>
            <Option value={2}></Option>
          </Select>
        </Form.Item>
        <Form.Item
          label=""
          name="threshold_min"
          rules={[{ required: true, message: "" }]}
        >
          <InputNumber  placeholder="" />
        </Form.Item>
        <Form.Item
          label=""
          name="threshold_max"
          rules={[{ required: true, message: "" }]}
        >
          <InputNumber  placeholder="" />
        </Form.Item>
        <Form.Item
          label=""
          name="trigger_fac"
          rules={[{ required: true, message: "" }]}
        >
          <InputNumber  placeholder="" />
        </Form.Item>
        <Form.Item
          label=""
          name="trigger_delta_fac"
          rules={[{ required: true, message: "" }]}
        >
          <InputNumber  placeholder="" />
        </Form.Item>
        <Form.Item
          label=""
          name="score_min"
          rules={[{ required: true, message: "" }]}
        >
          <InputNumber  placeholder="" />
        </Form.Item>
        <Form.Item
          label=""
          name="score_max"
          rules={[{ required: true, message: "" }]}
        >
          <InputNumber  placeholder="" />
        </Form.Item>
        <Form.Item
          label=""
          name="status"
          rules={[{ required: true, message: "" }]}
        >
          <Select placeholder="">
            <Option value={0}></Option>
            <Option value={1}></Option>
          </Select>
        </Form.Item>
      </Form>
    </Modal>
  );
};
export default memo(Index);
