import React, { memo, useState, useEffect } from "react";
import {
  Modal,
  Form,
  Input,
  Select,
  Button,
  Space,
  Row,
  Col,
  message,
  InputNumber,
} from "antd";
import { Tree } from "antd";
import { room_config_add, room_config_update } from "../../../../api/room";
// import { role_create } from "../../../api/role";
const { Option } = Select;
const { TextArea } = Input;
const Index = (props) => {
  const { onCancel, visible, onOk, info } = props;
  const [form] = Form.useForm();
  //
  const submit = async () => {
    // console.log(form.getFieldsValue());
    try {
      form.validateFields().then(async () => {
        await room_config_update({
          ...form.getFieldsValue(),
        });
        message.success("");
        onOk();
        form.resetFields();
      });
    } catch (e) {
      console.error(e);
    }
  };
  const FormConfig = {
    labelCol: { span: 9 },
    wrapperCol: { span: 15 },
  };

  const on_cancel = () => {
    onCancel();
    form.resetFields();
  };
  useEffect(() => {
    if (visible) {
      form.setFieldsValue({
        ...info,
      });
    }
  }, [visible]);
  return (
    <Modal
      title=""
      visible={visible}
      onCancel={on_cancel}
      onOk={submit}
    >
      <Form
        {...FormConfig}
        form={form}
        name="form_in_modal"
        hideRequiredMark={true}
      >
        <Form.Item
          label="id"
          name="rid"
          rules={[{ required: true, message: "id" }]}
        >
          <InputNumber disabled placeholder="id" />
        </Form.Item>
        {/* <Form.Item
          label=""
          name="room_type"
          rules={[{ required: true, message: "" }]}
        >
          <Select placeholder="">
            <Option value={1}>1-13Points</Option>
            <Option value={2}>2-10Card</Option>
            <Option value={3}>3-Pool</Option>
          </Select>
        </Form.Item> */}
        <Form.Item
          label=""
          name="human_min"
          rules={[{ required: true, message: "" }]}
        >
          <InputNumber placeholder="" />
        </Form.Item>
        <Form.Item
          label=""
          name="human_max"
          rules={[{ required: true, message: "" }]}
        >
          <InputNumber placeholder="" />
        </Form.Item>
        <Form.Item
          label=""
          name="wingman_min"
          rules={[{ required: true, message: "" }]}
        >
          <InputNumber placeholder="" />
        </Form.Item>
        <Form.Item
          label=""
          name="wingman_max"
          rules={[{ required: true, message: "" }]}
        >
          <InputNumber placeholder="" />
        </Form.Item>

        <Form.Item
          label=""
          name="buy_in_min"
          rules={[{ required: true, message: "" }]}
        >
          <InputNumber placeholder="" />
        </Form.Item>
        <Form.Item
          label=""
          name="buy_in_max"
          rules={[{ required: true, message: "" }]}
        >
          <InputNumber placeholder="" />
        </Form.Item>

        <Form.Item
          label=""
          name="join_interval_min"
          rules={[{ required: true, message: "" }]}
        >
          <InputNumber placeholder="" />
        </Form.Item>
        <Form.Item
          label=""
          name="join_interval_max"
          rules={[{ required: true, message: "" }]}
        >
          <InputNumber placeholder="" />
        </Form.Item>

        <Form.Item
          label=""
          name="wait_2_leave_time_min"
          rules={[
            { required: true, message: "" },
          ]}
        >
          <InputNumber placeholder="" />
        </Form.Item>
        <Form.Item
          label=""
          name="wait_2_leave_time_max"
          rules={[
            { required: true, message: "" },
          ]}
        >
          <InputNumber placeholder="" />
        </Form.Item>

        <Form.Item
          label=""
          name="leave_round_min"
          rules={[{ required: true, message: "" }]}
        >
          <InputNumber placeholder="" />
        </Form.Item>
        <Form.Item
          label=""
          name="leave_round_max"
          rules={[{ required: true, message: "" }]}
        >
          <InputNumber placeholder="" />
        </Form.Item>

        <Form.Item
          label=""
          name="leave_win_score_min"
          rules={[{ required: true, message: "" }]}
        >
          <InputNumber placeholder="" />
        </Form.Item>
        <Form.Item
          label=""
          name="leave_win_score_max"
          rules={[{ required: true, message: "" }]}
        >
          <InputNumber placeholder="" />
        </Form.Item>

        <Form.Item
          label=""
          name="leave_lose_score_min"
          rules={[{ required: true, message: "" }]}
        >
          <InputNumber placeholder="" />
        </Form.Item>
        <Form.Item
          label=""
          name="leave_lose_score_max"
          rules={[{ required: true, message: "" }]}
        >
          <InputNumber placeholder="" />
        </Form.Item>

        <Form.Item
          label="//"
          name="action_time_min"
          rules={[
            { required: true, message: "//" },
          ]}
        >
          <InputNumber placeholder="//" />
        </Form.Item>
        <Form.Item
          label="//"
          name="action_time_max"
          rules={[
            { required: true, message: "//" },
          ]}
        >
          <InputNumber placeholder="//" />
        </Form.Item>
      </Form>
    </Modal>
  );
};
export default memo(Index);
