import React, { memo, useState, useEffect } from "react";
import {
  Modal,
  Form,
  Input,
  Select,
  Button,
  Space,
  Row,
  Col,
  message,
  Radio,
} from "antd";
import { Tree } from "antd";
import {
  discard_config_create,
  discard_config_get,
  discard_config_update,
} from "../../../../api/room";
// import { accrount_create } from "../../../api/acount";
// import { role_create } from "../../../api/role";
const { Option } = Select;
const { TextArea } = Input;
const Index = (props) => {
  const { onCancel, visible, onOk, id } = props;
  const [state, set_state] = useState(0); //01
  const [form] = Form.useForm();
  //
  const submit = async () => {
    // console.log(form.getFieldsValue());
    try {
      form.validateFields().then(async () => {
        if (state == 0) {
          await discard_config_create({
            ...form.getFieldsValue(),
            rid: id,
          });
          message.success("");
        } else {
          await discard_config_update({
            ...form.getFieldsValue(),
            rid: id,
          });
          message.success("");
        }

        onOk();
        form.resetFields();
      });
    } catch (e) {
      console.error(e);
    }
  };
  const FormConfig = {
    labelCol: { span: 6 },
    wrapperCol: { span: 18 },
  };

  const on_cancel = () => {
    onCancel();
    form.resetFields();
  };
  //
  const discard_config_get_h = async () => {
    let data = await discard_config_get({ rid: id });
    // console.log(data);

    if (data) {
      set_state(1);
      form.setFieldsValue({
        ...data,
      });
    } else {
      set_state(0);
    }
  };
  useEffect(() => {
    if (visible) {
      discard_config_get_h();
      form.setFieldsValue({
        rid: id,
      });
    }
  }, [visible]);
  return (
    <Modal
      title=""
      visible={visible}
      onCancel={on_cancel}
      onOk={submit}
    >
      <Form
        {...FormConfig}
        form={form}
        name="form_in_modal"
        hideRequiredMark={true}
      >
        <Form.Item
          label="ID"
          name="rid"
          rules={[{ required: true, message: "ID" }]}
        >
          <Input disabled placeholder="ID" />
        </Form.Item>

        <Form.Item
          label=""
          name="pool_type"
          rules={[{ required: true, message: "" }]}
        >
          <Select placeholder="">
            <Option value={1}>1-</Option>
            <Option value={2}>2-</Option>
          </Select>
        </Form.Item>
        <Form.Item
          label=""
          name="risk_flag"
          rules={[{ required: true, message: "" }]}
        >
          <Select>
            <Option value={1}>1-</Option>
            <Option value={2}>2-</Option>
          </Select>
        </Form.Item>
        <Form.Item
          label=""
          name="discard_mode"
          rules={[{ required: true, message: "" }]}
        >
          <Radio.Group defaultValue={1}>
            <Radio value={1}></Radio>
            <Radio value={2}></Radio>
          </Radio.Group>
        </Form.Item>
      </Form>
    </Modal>
  );
};
export default memo(Index);
